import React from 'react'
import {useContext} from 'react'
import {incomeExpenseContext} from '../store/store'
import DeleteIcon from '@mui/icons-material/Delete';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Paper from '@mui/material/Paper';
import { Container, Typography,Grid, } from '@mui/material';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

function History(){
    const[state,dispatch]= useContext(incomeExpenseContext);
    return(
        <Typography><br/>
          <Container fixed>
            <Grid container spacing={2}>
          <Grid lg={8} md={6} sm={6} xs={6} >
   <Typography  align="left" variant="h4">History </Typography>
</Grid>
<Grid lg={4} md={6} sm={6} xs={6} >
   <Button class="btn" size="large"  style={{backgroundColor:"blue", color:"white",fontWeight:600}}  variant="contained" align="right" variant="h4" onClick={
      ()=>{
        dispatch({
          type:"CLEAR_HISTORY",
        })
      }
    }>Clear History </Button>
</Grid>
</Grid>
<List className="list">
    {state.incomeArray.map((item,index)=>{
      return(
        <ListItem>Income= {item.income}, Month= {item.month},  Date= {item.date}
         <Button align="right" style={{marginRight:"10px", textAligh:"right"}} className="delete" variant="text" startIcon={<DeleteIcon />} onClick={(v) => {

          dispatch({
            type: "DELETE_INCOME_HISTORY_ITEM",
            payload: index,
            income:item.income,
         })
}}>

        </Button></ListItem>
      )
        
    })}
       {state.expenseArray.map((item,index)=>{
      return(
        <ListItem>Income= {item.expense}, Month= {item.month},  Date= {item.date}
         <Button style={{marginRight:"10px !important", textAligh:"right"}} className="delete" variant="text" startIcon={<DeleteIcon align="right"  />} onClick={(v) => {

          dispatch({
            type: "DELETE_EXPENSE_HISTORY_ITEM",
            payload: index,
            expense:item.expense,
         })
}}>

        </Button></ListItem>
      )
        
    })}
</List>
</Container>
        </Typography>
    )
}
export default History;