import React,{ useState,useContext} from 'react'
import {incomeExpenseContext} from '../store/store'
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import { styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import Button from '@mui/material/Button';
import { Container, Typography } from '@mui/material';
import TotalIncomeExpense from './totalIncomeExpense'
import IncomeExpenseHeading from './incomeExpenseheading'
import History from './history'
const Item = styled(Paper)(({ theme }) => ({
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  }));


function AddIncomeExpense (){
    const [state, dispatch] = useContext(incomeExpenseContext);

    let [month,setMonth] = useState();
    let [income,setIncome]= useState();
    let [date,setDate] = useState(new Date().toLocaleString());

    function submitHandler(v){
        v.preventDefault();
       let sendtoDispatch={
           month,
           income,
           date,
       }
       setDate(date=new Date().toLocaleString());
      
      let inco= income.charAt(0);
       console.log('ddd',inco);
       if(income.charAt()=== "+"){
    dispatch({
        type:'ADD_INCOME',
        payload: sendtoDispatch,
    });
    console.log(sendtoDispatch);
    
    
    }
    if(income.charAt()=== "-"){
        dispatch({
            type: "ADD_EXPENSE",
            payload: sendtoDispatch,
        });
    }
    setMonth(month="");
    setIncome(income="");
}

    return(
        <div>
           <Container fixed>
           <TotalIncomeExpense/>
            <br/>
            <IncomeExpenseHeading/>
            <br/>
            <History/>
            <br/>
            <Grid container>
            <Grid lg={3} md={3} sm={12} xs={12}></Grid>
            <Grid    lg={6} md={6} sm={12} xs={12}>
             <FormControl style ={{width: '100%'}}  className="form" required variant="standard" className="select textfield " sx={{ m: 1,  }}>
            <TextField  className="form" type='text' id="standard-basic" value={month} label="Description" variant="standard" onChange={(e)=>{setMonth(e.target.value)}} /><br/><br/>
            <Typography variant="p">if (income) start with "+" sign ,if (expense) start with "-" sing </Typography>
            <TextField   className="form" type='text' id="standard-basic" value={income} label="+Income / -Expense" variant="standard" onChange={(e)=>{setIncome(e.target.value)}} /><br/><br/>
            
            <Button className="form"  size="small" variant="contained" onClick={submitHandler}><Typography variant="h6">Add Transction</Typography></Button>
            </FormControl>
            </Grid>
            <Grid lg={3} md={3} sm={12} xs={12}></Grid>
            </Grid>
            <br/>
      
      
    </Container>
      
       </div>
    )
}

export default AddIncomeExpense;