const reducer = (state, action) => {
  state = { ...state };
 console.log('initialState', state);

    switch (action.type) {
      case 'ADD_INCOME':
          console.log('what are',action);
          let data ={
           month : action.payload.month,
           income :action.payload.income,
           date :action.payload.date,
          }
          state.month = action.payload.month;
          state.income = action.payload.income.substring(1);
          state.total += parseInt(state.income);
          state.incomeArray = state.incomeArray.concat(data);
          console.log('incomeArray', state.incomeArray);
          console.log('data in income reducer', state);
        return state;

      case 'ADD_EXPENSE':
          console.log('whats expense',action)
          let data1 ={
            month : action.payload.month,
            expense :action.payload.income,
            date :action.payload.date,
           }
           state.month = action.payload.month;
           state.expense=action.payload.income.substring(1);
           state.total = parseInt(state.total)-parseInt(state.expense);
           state.expenseArray = state.expenseArray.concat(data1);
           console.log('expenseArray', state.expenseArray);
           console.log('data in expense reducer', state);
           return state;

           case 'DELETE_INCOME_HISTORY_ITEM':
            state.incomeArray.splice(action.payload,1);
            state.income = action.income.substring(1);
            state.total = parseInt(state.total)-parseInt(state.income);
            return state;
            case 'DELETE_EXPENSE_HISTORY_ITEM':
              state.expenseArray.splice(action.payload,1);
              state.expense = action.expense.substring(1);
              state.total =  parseInt(state.total)+parseInt(state.expense);
              return state;
              case 'CLEAR_HISTORY':
                state.incomeArray = [];
                state.expenseArray = [];
                state.income=0;
                state.expense= 0;
                state.total = 0;
                return state;
        
      default:
        return {
          state
        };
    }
  };
  export default reducer;