import { Container, Typography,Grid,Paper } from '@mui/material';
import { styled } from '@mui/material/styles';
import {useContext} from 'react'
import{ incomeExpenseContext} from '../store/store'
const Item = styled(Paper)(({ theme }) => ({
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  }));

function TotalIncomeExpense(){
    const [state]= useContext(incomeExpenseContext);
    return(
        <Typography>
           
            <Container fixed>
            <Grid container  >
        <Grid item md={12} sm={12} xs={12}>
          <Item><Typography class="app_heading" variant="h1" align="center">Expense Tracker App</Typography></Item>
        </Grid>
        </Grid><br/>
<Grid md={12}>
    <Item><Typography  align="left" variant="h5">Total Balance = {state.total} Pkr</Typography></Item>
</Grid><br/>

            </Container>
        </Typography>
    )
}
export default TotalIncomeExpense;