import { Container, Typography,Grid,Paper } from '@mui/material';
import { styled } from '@mui/material/styles';
import{ incomeExpenseContext} from '../store/store'
import {useContext} from 'react'
const Item = styled(Paper)(({ theme }) => ({
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  }));

function IncomeExpenseHeading(){
    const [state]= useContext(incomeExpenseContext);
    return(
        <Typography>
            <Container fixed>
           <Grid container spacing={2} >
<Grid lg={6} md={6} sm={6} xs={12} >
    <Item><Typography  align="center" variant="h5">+ Income {state.income} </Typography></Item>
</Grid>
<Grid lg={6} md={6} sm={6} xs={12}>
    <Item><Typography  align="center" variant="h5">- Expense {state.expense} </Typography></Item>
</Grid>
</Grid>
            </Container>
        </Typography>
    )
}
export default IncomeExpenseHeading;