import React,{useReducer,createContext} from 'react'
import reducer from '../reducer/incomeExpenseReducer'
export const incomeExpenseContext = createContext();



export  const IncomeExpenseContextProvider = (props) =>{
    const initialState = {
          month:'january',
          income:0.00,
          incomeArray:[],
          expense:0.00 ,
          expenseArray:[],
          total:0.00 ,
          date:new Date().toLocaleString(),

    }
    const[state,dispatch] = useReducer(reducer,initialState);
    console.log('state =',state);
    return(
        <div>
            { console.log('state ok is dkd',state ,reducer)}
            
    <incomeExpenseContext.Provider value={[state,dispatch]}>
       {  console.log('state ok',state ,reducer)}
    {props.children}
    </incomeExpenseContext.Provider>
    </div>
    )
}